<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12">
        <span class="text-h5 font-weight-medium">Informações</span>
      </v-col>
      <v-col cols="6" class="pb-0">
        <v-file-input
          label="Selecione o arquivo '.csv'"
          accept=".csv"
          outlined
          dense
          show-size
          truncate-length="35"
          @change="loadCsvLocal"
        ></v-file-input>
      </v-col>
      <v-col cols="12" class="py-0 font-weight-light">
        <span>
          Arquivo: <span class="font-weight-regular">{{ csvLocalFileName || 'aguardando seleção...'  }}</span>,
          tamanho: <span class="font-weight-regular">{{ csvLocalFileSizeHuman }}</span>
        </span>
      </v-col>
      <v-col cols="12" class="py-0 font-weight-light">
        <span>Linhas CSV carregadas: {{ pivottableData.length }}</span>
      </v-col>
      <v-col cols="4">
        <v-btn
          block
          elevation="0"
          color="blue"
          class="white--text"
          @click="openCsvViewer"
        >{{ showCsvViewer ? 'Esconder CSV' : 'Mostrar CSV' }}</v-btn>
      </v-col>
    </v-row>

    <v-row  v-show="showCsvViewer">
      <v-col cols="12">
        <CsvViewer ref="csvVieweComponent" />
      </v-col>
    </v-row>

    <v-row v-show="!showCsvViewer">
      <v-col cols="12">
        <BIProcessamento :pivottableData="pivottableData"/>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import papaparse from "papaparse";

import util from "../services/util";
import csvRipperUtil from "../services/csv-ripper-util";

import BIProcessamento from "../components/BIProcessamento.vue";
import CsvViewer from "../components/CsvViewer.vue";

export default {
  name: 'BICsvLocal',
  title: 'BI CSV',

  components: {
    BIProcessamento,
    CsvViewer
  },

  data: () => ({
    pivottableData: [],
    csvLocalFileName: null,
    csvLocalFileSize: null,
    showCsvViewer: false
  }),

  computed: {
    csvLocalFileSizeHuman: function () {
      return this.csvLocalFileSize !== null ? util.formatBytes(this.csvLocalFileSize) : 'aguardando seleção...'
    }
  },

  methods: {
    loadCsvLocal: function (file) {
      if (!file || !file.name) {
        console.log('Nenhum arquivo selecionado')
        this.csvLocalFileName = null
        this.csvLocalFileSize = null
        return
      }

      this.showCsvViewer = false
      this.csvLocalFileName = file.name
      this.csvLocalFileSize = file.size
      this.loadPivottableData(file)
    },
    loadPivottableData: function (file) {
      this.showToastLock('AGUARDE...', `Carregando o arquivo ${file.name}`)
      
      papaparse.parse(file, {
        download: false,
        skipEmptyLines: true,
        error: function(e) {
          alert(e)
        },
        complete: (results, fileRes) => {
          console.log('results', results, fileRes, this.pivottableData)
          const data = csvRipperUtil.convertCSVRipperToNumber(results.data)
          this.pivottableData.splice(0, this.pivottableData.length)
          this.pivottableData.push(...data)
          this.$swal.close()
        }
      })
    },
    openCsvViewer: function () {
      this.showCsvViewer = !this.showCsvViewer
      if (this.showCsvViewer) {
        this.showToastLock('Carregando CSV...', 'Atenção: Para grande quantidade de dados, o navegador pode ficar lento durante o carregamento. Apenas aguarde.')
        setTimeout(() => {
          this.$refs.csvVieweComponent.loadData(this.pivottableData)
          this.$swal.close()
        }, 500)
      } else {
        this.$refs.csvVieweComponent.loadData(null)
      }
    },
    showToastLock: function(title, message, lock=true) {
      this.$swal({
        showConfirmButton: !lock,
        allowEscapeKey: !lock,
        allowEnterKey: !lock,
        allowOutsideClick: !lock,
        title: title,
        text: message,
        icon: 'info',
      });
    }
  }

}
</script>
